import { Colors } from "../Colors";


export const Serv = ({iconLink, label, text}) => {
  return (
    <div className='serv'>
      <img src={iconLink} alt="" />
      <h2 style={{color: Colors.mainColor}}>{label}</h2>
      <p style={{color: Colors.mainColor}}>{text}</p>
    </div>
  )
}

export default Serv;

