import { Colors } from '../Colors';
import { FaBars } from 'react-icons/fa';
import './Header.css';
import { useState } from 'react';
const Header = () => {
  const [showMenu, setShowMenu] = useState(false);
  const clickMenu = () => {
    setShowMenu(!showMenu);
  };
  return (
    <header id='header'>
      <div className='logo' style={{ color: Colors.mainColor }}>
        Doob
      </div>
      <div className='container'>
        <FaBars
          className={`menu ${showMenu ? 'show' : ''}`}
          style={{ color: Colors.mainColor }}
          onClick={clickMenu}
        />
        <div className={`links ${showMenu ? 'show' : ''}`}>
          <a href='#home' style={{ color: Colors.mainColor }} onClick={clickMenu}>
            Home.
          </a>
          <a href='#about-us' style={{ color: Colors.mainColor }} onClick={clickMenu}>
            About Us.
          </a>
          <a href='#portfolio' style={{ color: Colors.mainColor }} onClick={clickMenu}>
            Portfolio.
          </a>
          <a href='#blog' style={{ color: Colors.mainColor }} onClick={clickMenu}>
            Blog.
          </a>
          <a href='#contact-us' style={{ color: Colors.mainColor }} onClick={clickMenu}>
            Contact Us.
          </a>
        </div>
        <div className='buttons'>
          <div className='language' style={{ color: Colors.mainColor }} >
            EN
          </div>
          <a
            href='#contact-us'
            className='contact-us'
            style={{ color: Colors.secondaryColor, position: 'relative' }}
          >
            Contact Us <span className='line'></span>
          </a>
        </div>
      </div>
    </header>
  );
};

export default Header;
