import './About.css';
import Services from "./Services";
import AboutUs from './AboutUs';
import leftImage from '../../images/about-us-background-left.png';
import rightImage from '../../images/about-us-background-right.png';


export const About = () => {
  return (
    <div className='about' id='about-us'> 
      <img src={leftImage} className='left' alt="" style={{ position: 'absolute', top: '10%', left: 0, zIndex: -1, maxWidth: '20%'}}/>
      <img src={rightImage} className='right' alt="" style={{ position: 'absolute', top: '40%', right: 0, zIndex: -1 , maxWidth: '20%'}} />
      <Services/>
      <AboutUs/>
    </div>
  )
}
