import { Button } from "../Button"
import { Colors } from "../Colors"
import Serv from "./Serv"
import iconOne from '../../images/services-icon-1.png';
import iconTwo from '../../images/services-icon-2.png';

const Services = () => {
  const text = 'ipsum dolor sit, amet consectetur adipisicing elit. Pariatur soluta aliquam, est numquam dolorum facere iste?'

  return (
    <div className='services'>
      <div className="container">
        <div className="text">
          <h1 style={{color: Colors.mainColor}}>AnyThing You Need, We've Got You Covered</h1>
          <Button text='Get In Touch' link='#contact-us'/>
        </div>
        <Serv iconLink={iconOne} label='Web & Graphic Design' text={text}/>
        <Serv iconLink={iconTwo} label='Web & App Development' text={text}/>
      </div>
    </div>
  )
}

export default Services
