import './Landing.css';
import landingBackground from '../../images/landing-background.png';
import landingImage from '../../images/landing.png';
import { Colors } from '../Colors';
import { Button } from '../Button';

const landing = () => {
  return (
    <div className='landing'>
      <img src={landingBackground} alt="Logo" />
      <div className="container">
        <div className="text">
          <h1 style={{color: Colors.mainColor}}>The Spirit Of Digital Agency.</h1>
          <p>Lorem ipsum dolor sit, amet consectetur adipisicing elit. Pariatur soluta aliquam, est numquam dolorum facere iste provident voluptates?</p>
          <div className="buttons">
            <Button text='More About Us' link='#about-us'/>
            <a href='#contact' className="get-in-touch" style={{color: Colors.mainColor}}>Get In Touch.</a>
          </div>
        </div>
        <img src={landingImage} alt="" />
      </div>
    </div>
  )
}

export default landing
