import Colors from '../Colors';

const Card = ({ title, date, text, special }) => {
  return (
    <div className='card' style={{backgroundColor: special? Colors.secondaryColor: 'white'}}>
      <h4
        className='title'
        style={{ color: special ? 'white' : Colors.mainColor }}
      >
        {title}
      </h4>
      <div className='date' style={{ color: special ? 'white' : '#333' }}>
        {date}
      </div>
      <div
        className='text'
        style={{ color: special ? 'white' : Colors.mainColor }}
      >
        {text}
      </div>
      <div
        className='arrow'
        style={{ backgroundColor: special ? 'white' : Colors.secondaryColor }}
      ></div>
    </div>
  );
};

export default Card;
