import Card from './Card';
import Colors from '../Colors';
import './Blog.css';
import Label from '../Label';
import Arrows from '../Arrows';

const Blog = () => {
  return (
    <div className='blog' id='blog'>
      <div className='container'>
        <div className="head">
          <Label label='Check Our News' text='BLOG STORIES'/>
          <Arrows/>
        </div>
        <div className='content'>
          <Card
            title='New Adventure'
            date='17th March 2021'
            text='Lorem ipsum dolor sit amet consectetur adipisicing elit. Dolorum dolore, reprehenderit eum quos facere porro!'
            special={true}
          />
          <Card
            title='New Adventure'
            date='17th March 2021'
            text='Lorem ipsum dolor sit amet consectetur adipisicing elit. Dolorum dolore, reprehenderit eum quos facere porro!'
            special={false}
          />
          <Card
            title='New Adventure'
            date='17th March 2021'
            text='Lorem ipsum dolor sit amet consectetur adipisicing elit. Dolorum dolore, reprehenderit eum quos facere porro!'
            special={false}
          />
        </div>
      </div>
    </div>
  );
};

export default Blog;
